import Vue from "vue";
import Router from "vue-router";
import Login from "@/components/Login";
import Home from "@/components/Home";
import PowerBenefit from "@/components/power/PowerBenefit";
import FloodRisk from "@/components/floodrisk/FloodRisk";
import GeneralFlood from "@/components/designflood/general/GeneralFlood";
import TimeChangeDesign from "@/components/designflood/timechange/TimeChangeDesign";
import Runoff from "@/components/RunoffPredict/Runoff";
import store from '../store/index'
Vue.use(Router);
const routes = [
  {
    path: "/",
    name: "",
    hidden: true,
    component: Login
  },
  {
    path: "/home",
    name: "",
    component: Home,
    hidden: true
  },
  {
    path: "/home",
    component: Home,
    children: [
      {
        path: "/runoff",
        name: "",
        meta: {
          requireAuth: true,//
          keepAlive: true
        },
        component: Runoff,
      },
    ]
  },
  {
    path: "/home",
    component: Home,
    name: "",
    children: [
      {
        path: "/generalFlood",
        name: "", meta: {
          requireAuth: true,//
          keepAlive: true
        },
        component: GeneralFlood
      },
      {
        path: "/timeChangeDesign",
        name: "", meta: {
          requireAuth: true,//
          keepAlive: true
        },
        component: TimeChangeDesign
      }
    ]
  },
  {
    path: "/home",
    component: Home,
    children: [
      {
        path: "/powerBenefit",
        name: "", meta: {
          requireAuth: true,//
          keepAlive: true
        },
        component: PowerBenefit,
      },
    ]
  },
  {
    path: "/home",
    component: Home,
    children: [
      {
        path: "/floodRisk",
        name: "", meta: {
          requireAuth: true,//
          keepAlive: true
        },
        component: FloodRisk
      }
    ]
  }
]

// token
if (window.localStorage.getItem('token')) {
  store.commit(LOGIN, window.localStorage.getItem('token'))
}

const router = new Router({
  routes
});
export default router;



